import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, GiaSpSubItem } from '../../../../models/dtos/mlf/giaSpSubItem';
import { TIPO_GIA_SP_SUB_ITEM_ENUM_VALUES } from '../../../../models/enumerated/mlf/tipoGiaSpSubItemEnum';

interface GiaSpSubItemEditViewProps extends EditViewProps<GiaSpSubItem> {}

const GiaSpSubItemEditView: FC<GiaSpSubItemEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<GiaSpSubItem>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Ocorrência" wikiId={35}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 5 } }}
              id="codigo"
              placeholder="Informe o Código"
              isPrependLabel={true}
              label="Código"
            />
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 300 } }}
              id="descricao"
              placeholder="Informe a Descrição"
              isPrependLabel={true}
              label="Descrição"
            />
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 100 } }}
              id="fundamentacao"
              placeholder="Informe a Fundamentação Legal"
              isPrependLabel={true}
              label="Fundamentação Legal"
            />
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="tipo"
              isPrependLabel={true}
              label="Tipo"
              inputDropDownProps={{
                optionsProvider: TIPO_GIA_SP_SUB_ITEM_ENUM_VALUES,
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default GiaSpSubItemEditView;
